import { adapters } from "@rp/adapters"
import { HomeNativeCustomerIo } from "./homeNativeEvents.types"

const homeNativeCustomerIo = {
  sendWatchlistCardTap(data: HomeNativeCustomerIo["data"]) {
    const message: HomeNativeCustomerIo = {
      name: "HomePage:watchlistCardClickedOn",
      data,
    }
    adapters.customerIo.track(message)
  },
}

export { homeNativeCustomerIo }
